<template>
  <div>
    <h1>Lista dei cron</h1>
    <hr />
    <div class="row"></div>
    <div class="row">
      <div class="col-lg-12 col-sm-12">
        <table class="table">
          <thead>
            <tr>
              <!-- <th>name</th> -->
              <th>script</th>
              <th>cron</th>
              <th>mod</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in data" :key="index">
              <!-- <td>{{ item.name }}</td> -->
              <td>{{ item.script }}</td>
              <td>{{ item.cron }}</td>
              <td>{{ item.mod }}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      data: [],
    };
  },
  mounted() {
    this.submit();
  },
  methods: {
    submit() {
      var link = this.apiURL + "/varie/cron";
      this.$store.dispatch("loading");
      this.axios.get(link).then((r) => {
        this.data = r.data;
        this.$store.dispatch("loaded");
      });
    },
    
  },
};
</script>